import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide
  },
  data() {
    return {
      dataList: [],
      page: 1,
      loadShow: true,
      total:''
    }
  },
  methods: {
    handleBottom() {
      /*this.page++
      this.getList()*/
      if (this.total!==''&&this.dataList.length >= parseInt(this.total)){
        this.$refs.my_scroller.finishInfinite(true);
        return
      }
      http.post('/user/recommend', {
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.dataList = this.dataList.concat(res.content.list)
        this.total = res.content.count
        this.page++
        this.$refs.my_scroller.finishInfinite(false);
      })
    },
    getList() {
      http.post('/user/recommend', {
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.dataList = this.dataList.concat(res.content.list)
        if (this.dataList.length < parseInt(res.content.count)) {
          this.$refs.vueLoad.onBottomLoaded();
        } else {
          this.$refs.vueLoad.onBottomLoaded();
        }
      })
    },
  },
  created() {
    // this.getList()
  }
}



